
import { Component,OnInit,OnDestroy } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import {Loginservice} from "../loginservice";
import { NgForm } from "@angular/forms";
import { ActivatedRoute, ParamMap } from "@angular/router";
import { Router } from "@angular/router";

@Component({
  selector: 'Chat',
  templateUrl: './chat.html',
  styleUrls: ['./chat.css']
})

export class Chat implements OnInit{

 currentUserId;
 friendid;
 enterValue="";chat;
 chatcontent;
 currentUser;
 friend;
 interval;
constructor(public loginService: Loginservice,public http:HttpClient, public route: ActivatedRoute){}
 
 
	ngOnInit()
	{
		
		 this.route.paramMap.subscribe((paramMap: ParamMap) => 
	 {
		  
		  	this.currentUserId=paramMap.get("currentUserid");
			this.friendid=paramMap.get("friendid");
			console.log(this.currentUserId,this.friendid);
			
	
		  
	  });
	  
	  
	  this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/singleUser",{userid:this.currentUserId}).subscribe(response=>{
							this.currentUser=response.user;
						
						
						
						this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/singleUser",{userid:this.friendid}).subscribe(response=>{
							this.friend=response.user;
					
						});
						
											
		});
	  
	  
	  
	    this.interval=setInterval(()=>{
		
		
			  	this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/getchat",{friendid:this.friendid}).subscribe(response=>{
											this.chat=response.chat;
											this.chatcontent=response.chat.content;
											//console.log(this.chatcontent);
	   	         });
		 
		
		
		
		
		},200);


		
		
		
	}
	
	
	send(form :NgForm){
		console.log(form.value.content);
		this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/chat",{currentuserid:this.currentUserId,friendid:this.friendid,content:form.value.content}).subscribe(response=>{
			
	    });
		this.enterValue="";
	}
 
 
   ngOnDestroy(){
   		clearInterval(this.interval);
		
	this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/deletechatrequest",{friendid:this.friendid}).subscribe(response=>{
	 	
	 });
   }
 
}