import { Component,OnInit,OnDestroy } from '@angular/core';
import { FormGroup, FormControl, Validators } from "@angular/forms";
import {SearchService} from "../searchService";
import { Subscription } from "rxjs";
import { NgForm } from "@angular/forms";
import {Loginservice} from "../loginservice";
import { HttpClient } from "@angular/common/http";

//use two types of Form handling

@Component({
  selector: 'Search',
  templateUrl: './search.html',
  styleUrls: ['./search.css']

})

export class Search implements OnInit {

    length;users;resultUsers;
	notFound=false; usersShow;
	
	form: FormGroup;
	private usersSubscription: Subscription;
	MAX=1000;
	show=false;
	
	pagesize=2; pageIndex=0;
	searchValue;
	
	loading=false;
	paginStart=false;
	currentuserid;
	currentUser;
	
	
	
	constructor(public searchService: SearchService,public loginService: Loginservice,public http:HttpClient)
	{
		this.users=[]; //all the fetch users
		this.resultUsers=[]; //what to display(based on the search value)
		this.usersShow=[];
	}
	
	ngOnInit()
	{
	    
	      this.currentuserid=this.loginService.getUserId();
		  // get the current user
		  
		  this.http.post("https://peaceful-mountain-59628.herokuapp.com/singleUser",{userid:this.currentuserid}).subscribe(response=>{
		  	this.currentUser=response['user']; //get the current user
			//console.log("currentUser",response.user);
			
			
		  });
		  
		  
		  
	     
			this.form = new FormGroup
		 ({
			  search: new FormControl(null)

		});
		this.usersSubscription=this.searchService.gerUsersUpdateListener()
		.subscribe(users=>{
		    console.log("search update", users);
		    
		    this.loading=true;
			setTimeout(()=>{this.loading=false;this.paginStart=true;},500);
            this.usersShow=[];
			this.notFound=false;
			this.resultUsers=users.users;
			//console.log("init",this.resultUsers);
			this.length=users.users.length;
		    if(this.length>this.MAX){this.length=this.MAX;}//100
			
			if(this.length===0){this.notFound=true;}
			
			
			
			for(var i=0;i<this.pagesize;i++){
			    if(this.resultUsers[(this.pageIndex)*this.pagesize+i])
				{
					this.usersShow.push(this.resultUsers[(this.pageIndex)*this.pagesize+i]);
				}

			}
			//console.log("user show",this.usersShow[0].friends);
			
		});
		
		
	}
	
	onChangedPage(event){
		//console.log("event",event);
		this.usersShow=[];
		this.pagesize=event.pageSize;
		this.pageIndex=event.pageIndex;
		
					
		for(var i=0;i<this.pagesize;i++)
		{
			if(this.resultUsers[(this.pageIndex)*this.pagesize+i])
			{
				this.usersShow.push(this.resultUsers[(this.pageIndex)*this.pagesize+i]);
			}
		}
		
	}
	
	
	addFriend(form :NgForm){
		let target=form.value.userid;
		
		this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/addFriend",{currentUser:this.currentuserid,target:target}).subscribe(response=>{
			console.log("okoko");
		
		this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/getUsers",{searchValue:this.searchValue}).subscribe(users=>{
		
			this.loading=true;
			setTimeout(()=>{this.loading=false;this.paginStart=true;},100);
            this.usersShow=[];
			this.notFound=false;
			this.resultUsers=users.users;
			this.length=users.users.length;
		    if(this.length>this.MAX){this.length=this.MAX;}//100
			
			if(this.length===0){this.notFound=true;}
			
			
			
			for(var i=0;i<this.pagesize;i++)
			{
			    if(this.resultUsers[(this.pageIndex)*this.pagesize+i])
				{
					this.usersShow.push(this.resultUsers[(this.pageIndex)*this.pagesize+i]);
				}

			}
			
					  this.http.post("https://peaceful-mountain-59628.herokuapp.com/singleUser",{userid:this.currentuserid}).subscribe(response=>
					  {
		  					this.currentUser=response['user']; //update the current user after add friends
			
		              });
			
		});
		
		
		});
		
		
	
	}
	
	
	onSubmit(){
	 	this.pagesize=2;
		this.pageIndex=0;
	    
	    this.searchValue=this.form.value.search;
		//console.log(this.form.value.search);
		let searchValue=this.form.value.search;
		
		
		  this.http.post("https://peaceful-mountain-59628.herokuapp.com/singleUser",{userid:this.currentuserid}).subscribe(response=>
			{
		  					this.currentUser=response['user']; //update the current user after add friends
							
							this.searchService.search(searchValue);//search
			
		    });
		
		
		
	
		
	}
	ngOnDestroy()
	{
		this.usersSubscription.unsubscribe();
	}

}