import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";

import { Header } from "./Header/header";
import { Register } from "./Register/register";
import { Login } from "./login/login";
import { User } from "./user/user";
import { Search } from "./search/search";
import {RouteGuard} from "./routeGuard";
import { MyPage } from "./mypage/mypage";
import { Founder } from "./founder/founder";
import { Post } from "./post/post";
import { GetPosts } from "./getposts/getposts";
import { Chat } from "./chat/chat";
import { Fail } from "./fail/fail";



const routes: Routes = [
  { path: '', component: Founder },
  { path: 'register', component: Register },
  { path: 'login', component: Login},
  { path: 'user/:userid', component: User },
  { path: 'search', component: Search, canActivate: [RouteGuard] },
  { path: 'mypage/:userid', component: MyPage,canActivate: [RouteGuard] },
  { path: 'founder', component: Founder },
  { path: 'mypage/post/:userid', component: Post,canActivate: [RouteGuard] },
  { path: 'myposts/:userid', component: GetPosts,canActivate: [RouteGuard] },
  { path: 'friendposts/:userid', component: GetPosts,canActivate: [RouteGuard] },
  { path: 'chat/:currentUserid/:friendid', component: Chat,canActivate: [RouteGuard] },
   { path: 'fail', component: Fail }
  
  
 
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [RouteGuard]
})
export class AppRoutingModule {}


