import { Component,OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, Validators } from "@angular/forms";
import {mimeType} from "./validator";
import {Registerservice} from "../registerservice";
import { Subscription } from "rxjs";

@Component({
  selector: 'Register',
  templateUrl: './register.html',
  styleUrls: ['./register.css']
})

export class Register implements OnInit,OnDestroy {
 
  private registerStateSub: Subscription;
  match=true;
  registerState=true;
  fillInformation=false;
  form: FormGroup;
  imagePreview:string="";
  emptyimage=false;
  emptyusername=false;
  invalidEmail=false;
  
  repeatUser=false;
  private repeatUserSub: Subscription;
  
  constructor( public registersService: Registerservice)
  {
   
	registersService.set("not dummy");
	//console.log(registersService.get());
	
  }
  
  loadRegister()
  {
  
    this.registerState=false;
	setTimeout(()=>{
	 this.registersService.setFillInformation(true);
	 this.fillInformation=true;
	},500);
  }


   ngOnInit()
   {
  	// console.log("Register Init");
	
	 this.registerStateSub=this.registersService.getRegisterUpdateListener().subscribe((state)=>{
	 	this.fillInformation=state;
		 this.registerState=true;
		 //console.log("update",this.fillInformation);

	 });
	 
	 this.repeatUserSub=this.registersService.getrepeatUserStateUpdate().subscribe((repeat)=>{
	    console.log("repeat user");
	 	this.repeatUser=repeat;
	 });
	     
	 this.form = new FormGroup
	 ({
		  email: new FormControl(null),
		  password: new FormControl(null),
		  confirmpassword: new FormControl(null),
		  username: new FormControl(null),
		  image: new FormControl(null, 
		  {
			validators: [Validators.required],
			asyncValidators: [mimeType]
          })

    });
	 
	 
   }
   
   //copy
  onImagePicked(event: Event) 
 {
		const file = (event.target as HTMLInputElement).files[0];
		
		this.form.patchValue({ image: file });
		
		this.form.get("image").updateValueAndValidity();
		const reader = new FileReader();
		reader.onload = () => 
		{
		  this.imagePreview = reader.result as string;
		  //console.log(this.imagePreview);
		};
		reader.readAsDataURL(file);
  }
  //update [src] imagePreview
  
  
  
   
   //  this.form.patchValue({ image: file });
   
   onSubmit()  //no validation yet
   {

			 if (this.form.invalid) //check for photo -> require field
			{
			  console.log("invalid");
			  this.emptyimage=true;
			  
			}
			 else
			{
			  console.log("valid photo");
			  this.emptyimage=false;
			}
			 // console.log(this.form.value.email);
			  console.log(this.form.value);

			 if(this.form.value.password!==this.form.value.confirmpassword)
			 {
				this.match=false;
				
			 }
			 else{
				this.match=true;
				//redirect 
			 }
			 
			 if(this.form.value.username){
			 	this.emptyusername=false;
			 }else
			 {
			 	this.emptyusername=true;
			 }
			 
			 
			 
			 if(this.emptyimage||!this.match||this.emptyusername||this.invalidEmail||this.repeatUser){
			    console.log("something wrong");
				return;
			 }
		
		this.registersService.addUser(this.form.value);
		//this.registersService.addUser(this.form.value.image);
   }
   
   onChange(event){
   	//console.log(event.target.value);
	let enterValue=event.target.value;
	this.repeatUser=false;
		if(!enterValue.includes("@")||(enterValue.split("@").length-1)>1)
		{
			this.invalidEmail=true;
		}else{
			this.invalidEmail=false;
		}
   }
   
   onuserNameChange(event){
		 let enterValue=event.target.value;
		 if(enterValue){
			this.emptyusername=false;
		 }else{
			this.emptyusername=true;
		 }
   }
   
   ngOnDestroy()
   {
   	 this.registerStateSub.unsubscribe();
	 this.repeatUserSub.unsubscribe();
   }
   
}
