import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Subject } from "rxjs";
import { map } from "rxjs/operators";
import { Router } from "@angular/router";



@Injectable({ providedIn: "root" })
export class Loginservice {

    private authenticatedUpdate = new Subject<boolean>();
    private isAuthenticated=false;
	private token;
	private userid;
	
	private tokenTimer;
	
	
	getUserId(){
		return this.userid;
	}
	
	constructor(private http: HttpClient,private router: Router){

	}
	
	getIsAuth(){
		return this.isAuthenticated;
	}
	
	getToken(){
		return this.token;
	}
	
	getAuthenticatedUpdate(){
		return this.authenticatedUpdate.asObservable();
	}
	
	
	logOut(){
	    //console.log("logout");
		
		this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/logout",{userid:this.userid}).subscribe(response=>{
		
		});
		
		
		this.clearAuthData();
		clearTimeout(this.tokenTimer); // clear the autoLogout timer
		this.userid=null;
		this.isAuthenticated=false;
		this.token = null;
		this.authenticatedUpdate.next(this.isAuthenticated);
		this.router.navigate(["/"]);
		
	}
	
	login(formValue)
	{
    	let email=formValue.myemail;
		let password=formValue.mypassword;
		
		
        this.http
       .post<any>
	   (
        "https://peaceful-mountain-59628.herokuapp.com/login",{email:email,password:password}
       ).subscribe(response=>{
	      
		  //get back the token if login is succed
	      this.token=response.token;
		  
		  if(this.token){
		    const expiresInDuration = response.expiresIn;
			this.logoutAutomatically(expiresInDuration); //a timer
			
		    this.userid=response.userid;
		  	this.isAuthenticated=true;
			this.authenticatedUpdate.next(this.isAuthenticated);
			
			const now = new Date();
            const expirationDate = new Date(now.getTime() + expiresInDuration * 1000);
			console.log("expire date ",expirationDate);
			
			this.saveAuthData(this.token, expirationDate, this.userid);
			
			//path
			let path="/mypage/"+this.userid;
			this.router.navigate([path]);
		  }
	   
	   },err=>{
	   		this.router.navigate(["/fail"]);
	   
	   });
		
		
	}
	
	
	
  private saveAuthData(token: string, expirationDate: Date, userId: string) {
		localStorage.setItem("token", token);
		localStorage.setItem("expiration", expirationDate.toISOString());
		localStorage.setItem("userId", userId);
  }
  
  
    private clearAuthData() 
	{
		localStorage.removeItem("token");
		localStorage.removeItem("expiration");
		localStorage.removeItem("userId");
    }
	
    
	private logoutAutomatically(duration: number) {
		//console.log("Setting timer: " + duration);
		this.tokenTimer = setTimeout(() => {
		  this.logOut();
		  alert("Session Expire! Loging Out");
		}, duration*1000 );
    }
	
	
     autoAuthUser() 
	 {
			const authInformation = this.getAuthData();
			if (!authInformation) {
			  return;
			}
			const now = new Date();
			const expiresIn = authInformation.expirationDate.getTime() - now.getTime();
			if (expiresIn > 0) {
			  
			  this.token = authInformation.token;
			  this.isAuthenticated = true;
			  this.userid = authInformation.userId;
			  this.logoutAutomatically(expiresIn / 1000);
			  this.authenticatedUpdate.next(true);
			}
    }
	
	private getAuthData()
	{
		const token = localStorage.getItem("token");
		const expirationDate = localStorage.getItem("expiration");
		const userId = localStorage.getItem("userId");
		if (!token || !expirationDate) {
		  return;
		}
		return {
		  token: token,
		  expirationDate: new Date(expirationDate),
		  userId: userId
		};
    }

}
