import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Subject } from "rxjs";
import { map } from "rxjs/operators";
import { Router } from "@angular/router";



@Injectable({ providedIn: "root" })
export class SearchService {
    
    
	private usersUpdateListener = new Subject<any>();
	
	constructor(private http: HttpClient) 
	{
		
	}
	
	gerUsersUpdateListener(){
		return this.usersUpdateListener;
	}
	
	search(searchValue)
	{
      this.http
      .post<{ users:any }>
	  (
        "https://peaceful-mountain-59628.herokuapp.com/getUsers",{searchValue:searchValue}
      ).subscribe(users=>
	  {
	  	 //console.log("users",users);
		 //this.users=users;
		 
		 //fetching all the user.
		 console.log("search service",users)
		 this.usersUpdateListener.next(users);
	  },
	  err=>
	  {
	    console.log(err);
	  });
	
	}


}
