
import { Component,OnInit } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import {Loginservice} from "../loginservice";
import { NgForm } from "@angular/forms";
import { Router } from "@angular/router";

@Component({
  selector: 'Post',
  templateUrl: './post.html',
  styleUrls: ['./post.css']
})

export class Post implements OnInit{

	currentUserId;
	
	  constructor(private http: HttpClient,private loginservice: Loginservice,private router:Router) 
  {
  	
  }
	
	
	
	
	ngOnInit()
	{
		this.currentUserId=this.loginservice.getUserId();
		//console.log("post",this.currentUserId);
	}


	handlePost(form :NgForm )  //NgForm rather than form-group
 {
   
   //console.log("content",form.value.content);
   let title=form.value.title;
   let content=form.value.content;
   this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/savePost",{title:title,content:content}).subscribe(response=>{
   		let path="./mypage/"+this.currentUserId;
		this.router.navigate([path]);
   });
   
 }
 
 
}
