
import { Component,OnInit } from '@angular/core';
import {Registerservice} from "../registerservice";
import {Loginservice} from "../loginservice";
import { Subscription } from "rxjs";


@Component({
  selector: 'MyHeader',
  templateUrl: './header.html',
  styleUrls: ['./header.css']
})

export class Header implements OnInit {
   authenticated=false;
   smallenter=false;
   enter=false;
   myMenu=false;mysmallMenu=false; menu1=false;menu2=false;menu3=false;
   show=false;smallshow=false;
   smallmenu1=false;smallmenu2=false;smallmenu3=false;smallmenu4=false;smallmenu5=false;
   
    private authenticatedStateSub: Subscription;
	private userid;
    path="/";
   
   constructor( public registersService: Registerservice,public loginService: Loginservice){console.log("header constructor");}
   
      ngOnInit()
   {
            this.authenticated=this.loginService.getIsAuth();
         	this.userid=this.loginService.getUserId();
			if(this.userid){
				this.path=this.path+"mypage/"+this.userid;
			}
			
			
    	this.authenticatedStateSub=this.loginService.getAuthenticatedUpdate().subscribe((authenticate)=>{
			this.authenticated=authenticate;
			this.userid=this.loginService.getUserId();
			if(this.userid){
				this.path=this.path+"mypage/"+this.userid;
			}else{
				this.path="/";
			}
		});
   }
   
   register(){
   		this.registersService.setFillInformation(false);
   }
   
   
   logOut(){
   		this.loginService.logOut();
   }
   
   appear()
   {
        //console.log("appear");
        if(!this.show)
		{
		     //this.enter=true;
			 this.myMenu=true;
			 this.menu1=true;
			 setTimeout(()=>
			 {
				this.menu2=true;
					setTimeout(()=>
					{
					 this.menu3=true;
					 this.show=true;
					},100);
			 },100);
		 }
   }
   
   settrue()
   {
		//console.log("settrue");
		this.enter=true;
   }
   settrue1()
   {
		console.log("settrue");
		this.smallenter=true;
   }
   
   disappear()
   {
     

	 	if(this.show)
		{
			 this.menu3=false;
			 setTimeout(()=>
			 {
				this.menu2=false;
					setTimeout(()=>
					{
						 this.menu1=false;
						 this.myMenu=false;
						 this.show=false;
						 this.enter=false;
					},100);
			 },100);
		 }
   }
   
   disappear1()
   {
     setTimeout(()=>{
	 
	 	if(this.show&&!this.enter)
		{
			 this.menu3=false;
			 setTimeout(()=>
			 {
				this.menu2=false;
					setTimeout(()=>
					{
						 this.menu1=false;
						 this.myMenu=false;
						 this.show=false;
						 this.enter=false;
					},100);
			 },100);
		 }
	 },200);
   
   }
   
   
    smalldisappear1(){
	   setTimeout(()=>{
	   if(this.smallshow&&!this.smallenter)
		{
			 this.smallmenu5=false;
			 setTimeout(()=>
			 {
				this.smallmenu4=false;
					setTimeout(()=>{
					 this.smallmenu3=false;
					 	setTimeout(()=>{
						 this.smallmenu2=false;
						 	setTimeout(()=>
							{
								 this.smallmenu1=false;
								 this.mysmallMenu=false;
								 this.smallshow=false;
								 this.smallenter=false;
							},100);
						},100);
					},100);
			 },100);
		 }
	       
	   },200);
	}
   
   
   
   
   smallappear()
   {
   
           if(!this.smallshow)
		{
		     //this.smallenter=true;
			 this.mysmallMenu=true;
			 this.smallmenu1=true;
			 setTimeout(()=>
			 {
				this.smallmenu2=true;
					setTimeout(()=>{
					 this.smallmenu3=true;
					 	setTimeout(()=>{
						 this.smallmenu4=true;
						 	setTimeout(()=>
							{
								 this.smallmenu5=true;
								 this.smallshow=true;
							},100);
						},100);
					},100);
			 },100);
		 }
   }
   smalldisappear()
   {
   	           if(this.smallshow)
		{
			 this.smallmenu5=false;
			 setTimeout(()=>
			 {
				this.smallmenu4=false;
					setTimeout(()=>{
					 this.smallmenu3=false;
					 	setTimeout(()=>{
						 this.smallmenu2=false;
						 	setTimeout(()=>
							{
								 this.smallmenu1=false;
								 this.mysmallMenu=false;
								 this.smallshow=false;
								 this.smallenter=false;
							},100);
						},100);
					},100);
			 },100);
		 }
   }
}
