
import { Component,OnInit } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import {Loginservice} from "../loginservice";
import { NgForm } from "@angular/forms";
import { Router } from "@angular/router";
import { ActivatedRoute, ParamMap } from "@angular/router";

@Component({
  selector: 'GetPost',
  templateUrl: './getposts.html',
  styleUrls: ['./getposts.css']
})

export class GetPosts implements OnInit{
	
	userId;
	posts=[];
	user={name:"a"};
	currentUserId;
	constructor(public loginService: Loginservice,public http:HttpClient,public route: ActivatedRoute){
	
	}

	ngOnInit(){
	
			this.currentUserId=this.loginService.getUserId();

			this.route.paramMap.subscribe((paramMap: ParamMap) => 
		 {
			  if (paramMap.has("userid")) 
			  {
					this.userId=paramMap.get("userid");
					this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/getposts",{userId:this.userId}).subscribe(response=>{
						//console.log(response.posts);
						this.posts=response.posts;
						
						this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/singleUser",{userid:this.userId}).subscribe(response=>{
							this.user=response.user;
						});
						
					});

			  }
		  });
		
	}
	
	deletepost(event){
		//console.log("delete post",event.target.value);
		let deletepostid=event.target.value;
		
		this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/deletepost",{deleteid:deletepostid}).subscribe(response=>{
			
			this.http.post<any>("https://peaceful-mountain-59628.herokuapp.com/getposts",{userId:this.userId}).subscribe(response=>{
						//console.log(response.posts);
						this.posts=response.posts;});
		});
		
	}

 
}